.post-container {
    width: 100%;
    max-width: var(--form-max-width);
    margin-inline: auto;
}

#post .posts-item p {
    display: block;
}

.post-footer {
    display: flex;
    justify-content: space-between;
    margin-top: 1rem;
}

.post-footer button {
    width: 30%;
}

.delete-btn {
    color: var(--color-red);
}